import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import CommentIcon from '@material-ui/icons/Comment';
import { selectCategories } from '../../categorySlice';
import { getCategories } from '../../categoryService';
import {
  selectFilteredCategories,
  setFilteredCategories,
} from '../../../phrase/phraseSlice';

const useStyles = makeStyles({
  listItem: {
    width: '10px',
    'list-style-type': 'none',
  },
});

export default function CategoryList() {
  const classes = useStyles();
  const categories = useSelector(selectCategories);
  const dispatch = useDispatch();
  const checked = useSelector(selectFilteredCategories);

  useEffect(() => {
    if (!categories.length) {
      dispatch(getCategories());
    }
  }, []);
  const handleToggle = (category) => () => {
    const currentIndex = checked.indexOf(category.id);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(category.id);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    return dispatch(setFilteredCategories(newChecked));
  };

  return (
    <Grid container spacing={3}>
      {categories.map((category) => (
        <Grid item xs={3} key={category.id} className={classes.listItem}>
          <ListItem
            role={undefined}
            dense
            button
            onClick={handleToggle(category)}
          >
            <ListItemIcon>
              <Checkbox
                edge="start"
                checked={checked.indexOf(category.id) !== -1}
                tabIndex={-1}
                disableRipple
                inputProps={{ 'aria-labelledby': category.id }}
              />
            </ListItemIcon>
            <ListItemText id={category.id} primary={category.name} />
            <ListItemSecondaryAction>
              <IconButton edge="end" aria-label="comments">
                <CommentIcon />
              </IconButton>
            </ListItemSecondaryAction>
          </ListItem>
        </Grid>
      ))}
    </Grid>
  );
}
