import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { getPhrases } from '../../phraseService';
import { selectPhrases } from '../../phraseSlice';

export default function WordInput({ onChange, ...props }) {
  const phrases = useSelector(selectPhrases);
  const dispatch = useDispatch();
  const [options, setOptions] = React.useState([]);

  React.useEffect(() => {
    if (!phrases.length) {
      dispatch(getPhrases());
    }
    setOptions(phrases.map((option) => `${option.title}`));
  }, [phrases]);

  return (
    <Autocomplete
      {...props}
      fullWidth
      freeSolo
      onClose={(e) => onChange(e)}
      disableClearable
      id="phrase-input"
      options={options}
      renderInput={(params) => <TextField {...params} label="Title" />}
    />
  );
}
