import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Chip from '@material-ui/core/Chip';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';

async function speak(text) {
  const speechSyn = new SpeechSynthesisUtterance(text);
  speechSyn.lang = 'en-US';
  return window.speechSynthesis.speak(speechSyn);
}

const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  chip: {
    padding: '5px 20px 0px 20px',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

export default function PhraseItem({
  phrase,
  isUpdate,
  categories,
  handleOpen,
  handleDelete,
}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const phraseCategories = categories.filter(
    (item) => phrase.categories && phrase.categories.includes(item.id),
  );
  return (
    <Grid item>
      <Card className={classes.root}>
        <CardContent className={classes.chip}>
          {phraseCategories.length
            ? phraseCategories.map((item) => (
                <Chip key={item.id} label={item.name} color="primary" />
              ))
            : 'Word of the Day'}
        </CardContent>
        <CardContent>
          <Typography variant="h5" component="h2">
            {phrase.title}
          </Typography>
          <Typography className={classes.pos} color="textSecondary" />
          <Typography variant="body2" component="p">
            {phrase.meaning}
            <br />
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            size="small"
            type="button"
            onClick={() => speak(phrase.title)}
          >
            <VolumeUpIcon />
          </Button>
          {isUpdate && (
            <Button
              size="small"
              type="button"
              onClick={() => handleOpen(phrase)}
            >
              Edit
            </Button>
          )}
          {isUpdate && (
            <Button size="small" type="button" onClick={handleClickOpen}>
              Delete
            </Button>
          )}
        </CardActions>
      </Card>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Are you sure to remove this phrase?
        </DialogTitle>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button
            onClick={() => handleDelete(phrase)}
            color="secondary"
            autoFocus
          >
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Grid>
  );
}
