import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { getCategories } from 'features/category/categoryService';
import { selectCategories } from 'features/category/categorySlice';
import { selectUser } from 'features/auth/authSlice';
import PhraseItem from '../PhraseItem';
import { removePhrase, getPhrases, updatePhrase } from '../../phraseService';
import { selectPhrases, selectFilteredCategories } from '../../phraseSlice';
import PhraseModal from '../PhraseModal';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  list: {
    flexGrow: 1,
  },
  gridList: {},
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
}));

export default function PhraseList({ limit, isFilter = true }) {
  const phrases = useSelector(selectPhrases);
  const user = useSelector(selectUser);
  const filteredCategories = useSelector(selectFilteredCategories);
  const categories = useSelector(selectCategories);
  const [phrase, setPhrase] = useState();
  const classes = useStyles();
  const dispatch = useDispatch();

  const [open, setOpen] = useState(false);

  const handleOpen = (currentPhrase) => {
    setPhrase(currentPhrase);
    setOpen(true);
  };

  const handleDelete = async (currentPhrase) => {
    await dispatch(removePhrase(currentPhrase.id));
    return dispatch(
      getPhrases({
        isFilter,
        filteredCategories,
        limit: limit || 100,
        startAt: 0,
      }),
    );
  };

  const handleClose = () => {
    setOpen(false);
  };

  const updatePhraseAction = async (data, currentPhrase) => {
    await dispatch(
      updatePhrase({
        ...currentPhrase,
        ...data,
      }),
    );
    setOpen(false);
    return dispatch(
      getPhrases({
        isFilter,
        limit: limit || 100,
        startAt: 0,
      }),
    );
  };
  useEffect(() => {
    if (!phrases.length) {
      dispatch(
        getPhrases({
          isFilter,
          filteredCategories,
          limit: limit || 100,
          startAt: 0,
        }),
      );
    }
    if (!categories.length) {
      dispatch(
        getCategories({
          limit: 1000,
        }),
      );
    }
  }, []);

  return (
    <div className={classes.root}>
      <Grid container className={classes.list} spacing={2}>
        <Grid item xs={12}>
          <Grid container justify="center" spacing={2}>
            {phrases.map((phraseItem) => (
              <PhraseItem
                phrase={phraseItem}
                isUpdate={
                  user && (user.uid === phraseItem.userId || !phraseItem.userId)
                }
                categories={categories}
                key={phraseItem.id}
                handleOpen={handleOpen}
                handleDelete={handleDelete}
              />
            ))}
          </Grid>
        </Grid>
      </Grid>
      <PhraseModal
        open={open}
        handleClose={handleClose}
        phrase={phrase}
        updatePhrase={updatePhraseAction}
      />
    </div>
  );
}
